'use client';

import TeamOverview from '../../components/TeamOverview';
import useWorkspace from '../../hooks/useWorkspace';

export default function TeamPage() {
  const { teamMembers, loading, handleTeamCreate } = useWorkspace();

  return (
    <div className="space-y-6">
      <header className="surface-card space-y-2">
        <p className="text-xs font-semibold uppercase tracking-[0.35em] text-slate-400">Teams</p>
        <h1 className="text-3xl font-semibold text-slate-900">Team capacity</h1>
        <p className="text-sm text-slate-500">
          Keep an eye on assignments and rebalance workloads quickly.
        </p>
      </header>

      {loading ? (
        <div className="surface-card flex items-center justify-center text-slate-400">Loading...</div>
      ) : (
        <TeamOverview teamMembers={teamMembers} onCreate={handleTeamCreate} />
      )}
    </div>
  );
}
